body > #root > div {
  height: 100%;
  background-color: rgb(241, 242, 242);
}
pre {
  background: black;
  border: 1px solid #ddd;
  /* border-left: 3px solid #f2334c; */
  color: rgb(255, 255, 255);
  page-break-inside: avoid;
  font-family: monospace;
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1em 1.5em;
  display: block;
  word-wrap: break-word;
  white-space: pre-wrap;
}
code[class*="language-"],
pre[class*="language-"] {
  background: black;
  color: rgb(255, 255, 255);
  text-shadow: none;
  /* white-space: pre-wrap; */
}
.header {
  background-color: white;
  border-color: #dee0e1 !important;
  text-align: center;
  height: 55px;
  color: rgb(15, 43, 60);
  font-size: 0;
  font-weight: 900;
  font-family: monospace;
  border-bottom: 1px solid rgb(15, 43, 60);
}
.container {
  margin: auto;
  margin-top: 3%;
  width: 60%;
  padding: 10px;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: flex-start;
}
.container > .question-list {
  margin-bottom: 5%;
  background-color: white;
  /* border: 3px solid red; */
}
.card {
  border: none !important;
}
.card-title {
  font-weight: 700;
}
.card-text {
  font-size: 18px;
  font-style: italic;
}
.inptotpt {
  margin-bottom: 4%;
}
.inptotpt > p {
  margin-bottom: 0%;
}

@media only screen and (max-width: 600px) {
  .user {
    padding-bottom: 20px;
  }
  .signin-btn {
    position: relative;
    width: 157px;
    height: 39px;
    top: 2%;
    right: 15%;
    color: white;
    font-size: 18px;
    background-color: rgb(16 17 19 / 82%);
  }
  .overlay {
    float: right;
    height: 100%;
    width: 35%;
    position: fixed;
    z-index: 1;
    top: 55px;
    right: 0;
    background-color: black;
    opacity: 0.5;
    transition: opacity 0.2s;
  }
  .sidenav {
    height: 100%;
    width: 65%;
    position: fixed;
    z-index: 1;
    top: 55px;
    left: 0;
    background-color: white;
    overflow-x: hidden;
    transition: opacity 0.2s;
    padding-top: 5px;
  }

  .sidenav a {
    padding: 8px 8px 19px 18px;
    text-decoration: none;
    font-size: 19px;
    color: rgb(15, 43, 60);
    display: block;
    text-align: left;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif,
      "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol",
      "Noto Color Emoji";
    font-weight: 300;
    border-bottom: 0.5px solid rgb(238, 238, 238);
  }

  .sidenav a:hover {
    color: black;
    font-weight: 600;
  }

  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 36px;
  }
  .container {
    margin: 0;
    width: 100%;
    padding: 0;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: flex-start;
  }
  .container > .question-list {
    margin-bottom: 5%;
    background-color: white;
    /* border: 3px solid red; */
  }
  .sidebar {
    position: fixed;
    background: white;
    width: 60%;
    height: 100%;
    z-index: 999;
    margin-top: 13%;
  }
  .card {
    border: none !important;
    width: 100% !important;
  }
  .card-title {
    font-weight: 700;
  }
  .card-text {
    font-size: 18px;
    font-style: italic;
  }
  .inptotpt {
    margin-bottom: 4%;
  }
  .detailsinptotpt {
    margin-top: 2%;
    margin-bottom: 5%;
  }
  .detailsinptotpt > p {
    margin-bottom: 0;
  }
  .inptotpt > p {
    margin-bottom: 0%;
  }
  .question-container {
    padding: 5%;
  }
  .star {
    position: relative;
    float: right;
  }
}
